define('services/page-manager',['pubsub'], function(pubsub) {
    var PageEvents = pubsub.create();
    var currentPage = 1;
    var totalPages = 31;

    return {
        setTotalPages: function(numberOfPages) {
            totalPages = numberOfPages;
        },
        getTotalPages: function() {
            return totalPages;
        },
        getPage: function() {
            return currentPage;
        },
        setPage: function(pageNumber) {
            if(pageNumber < 1) {
                if(currentPage === 1) {
                    return;
                }

                pageNumber = 1;
            }

            currentPage = pageNumber;

            this.Event.publish('page.changed', null, pageNumber);
        },
        Event: PageEvents
    }
});

