define('components/navigation-bar/index',[
    'ko',
    'text!components/navigation-bar/template.html',
    'services/signal',
    'services/page-manager',

    'less!components/navigation-bar/style.less'
], function(ko, template, Signal, PageManager) {
    ko.components.register('navigation-bar', {
        viewModel: (function() {
            var NavigationBar = function() {
                this.pageNumber = ko.observable(PageManager.getPage());
                this.totalPages = 31;

                this.totalAnswered = 0;
                this.totalQuestions = 31;
            };

            NavigationBar.prototype.__updatePage = function (page) {
                PageManager.setPage(page);
                this.pageNumber(PageManager.getPage());
            };

            NavigationBar.prototype.goToPrev = function() {
                var newPage = PageManager.getPage() - 1;
                this.__updatePage(newPage);
            };

            NavigationBar.prototype.goToNext = function() {
                var newPage = PageManager.getPage() + 1;
                this.__updatePage(newPage);
            };

            return NavigationBar;
        }()),

        template: template
    });
});

